import { Component, EventEmitter, Input, Optional, Output } from '@angular/core';
import { CashflowInputValues } from './CashflowInputComponentOutput';
import { FormBuilder } from '@angular/forms';

@Component({
  standalone: false,
  selector: 'app-cashflow-input',
  templateUrl: './cashflow-input.component.html',
  styleUrls: ['./cashflow-input.component.scss']
})
export class CashflowInputComponent {
  initailAmount = 1000;
  interestRatePercent = 4;
  compoundedPerPeriod = 1;
  period = 10;
  contributionPerPeriod = 0;

  @Output()
  inputComplete = new EventEmitter<CashflowInputValues>();

  @Input() inputValue: CashflowInputValues;
  constructor(
    private formBuilder: FormBuilder,
  ) {}
  DEFAULTS:CashflowInputValues = {initailAmount :1000,interestRatePercent:4,period:5,compoundedPerPeriod:1,contributionPerPeriod:0}
  checkoutForm = this.formBuilder.group({
    initailAmount:1000,
    interestRatePercent: 4,
    period: 5,
    compoundedPerPeriod: 1,
    contributionPerPeriod: 1
  })
  ngOnInit(): void {
    const {
      initailAmount,
      interestRatePercent,
      period,
      compoundedPerPeriod,
      contributionPerPeriod } = { ...this.DEFAULTS,...this.inputValue}
      this.initailAmount = initailAmount;
      this.interestRatePercent = interestRatePercent;
      this.period = period;
      this.compoundedPerPeriod = compoundedPerPeriod;
      this.contributionPerPeriod = contributionPerPeriod;
  }

  calculate(formValue: {
    initailAmount: number,
    interestRatePercent: number,
    compoundedPerPeriod: number,
    period: number,
    contributionPerPeriod: number
  }) {
    const { compoundedPerPeriod, initailAmount, interestRatePercent, period, contributionPerPeriod } = formValue;
    this.inputComplete.emit(
      { compoundedPerPeriod, initailAmount, interestRatePercent, period, contributionPerPeriod }
    )
  }

}
